#graph-container{background-color:#f5e3e3;margin:0 auto;margin-bottom:50px;height:75vh;border-radius:20px;position:relative;width:100%}.line{stroke-width:.40%;fill:none}.hover-info-container{position:absolute;background-color:rgba(0,0,0,0.8);border-radius:8px;padding:10px;text-align:center;flex-direction:column;font-weight:bold;text-shadow:0px 0px 6px black;font-size:20px}@media only screen and (max-device-width: 500px){.hover-info-container{font-size:10px}}.hover-overlay{background-color:rgba(0,0,0,0.4);pointer-events:all}.text{font-size:20px}@media (max-width: 500px){.text{font-size:10px}}
