body{background-color:#1c1c1c}.content{display:flex;flex-direction:column;background-color:#1c1c1c;width:100%;padding:5vh;box-sizing:border-box;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content}.site-header{align-self:center;font-size:40px;color:white;margin-bottom:35px}.top-row,.bottom-row{display:flex;justify-content:space-between;height:3vh;margin-bottom:35px}.left-top-row{display:flex;width:33vw;justify-content:space-between}#season-dropdown{width:15vw}#stat-dropdown{width:15vw}.right-top-row{display:flex;width:5vw;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content}.glossary-button{width:100%}.search-players-input-container{display:flex;flex-direction:column;width:18vw;margin-right:7vw;position:relative}#search-players-input{min-height:100%;box-sizing:border-box}#player-dropdown{background-color:grey;-webkit-animation-name:dropdown;animation-name:dropdown;-webkit-animation-duration:500ms;animation-duration:500ms;z-index:1}#player-dropdown li{min-height:3vh;padding:2px 8px;border-bottom:1px solid yellow;display:flex;align-items:center;overflow:hidden;box-sizing:border-box}.player-names-container{width:65.5vw;display:flex;background-color:rgba(0,0,0,0.5);color:white;align-items:center;padding:0 1vw;box-sizing:border-box;height:5vh;align-self:center}footer{display:flex;flex-direction:column;height:4vh;background-color:indianred}
